<template>
  <div id="app">
    <!-- Navbar Start -->
    <b-navbar id="navbar" fixed="top">
      <b-container>
        <b-navbar-brand href="#" id="navbarBrand" to="/">
          SEASON
          <vue-typed-js :strings="['<b>BD</b>']" :loop="true" :showCursor="false" id="navTypeJs">
            <span class="typing"></span>
          </vue-typed-js>
        </b-navbar-brand>
      </b-container> 
    </b-navbar>
    <!-- Navbar End -->
    <router-view></router-view>
  </div>
</template>
<script>
import Vue from 'vue';
import VueTypedJs from 'vue-typed-js';
Vue.use(VueTypedJs);
export default {
  name: 'app',
  data: function(){
    return {
      
    }
  },
  methods: {
    
  }
}
</script>
<style>
/** Global Fonts */
* {
  font-family: 'Montserrat', sans-serif;
}
/** Hide Scroll Bar */
::-webkit-scrollbar { 
  display: none; 
}
/** Header */
h1,
h2,
h3,
h4,
h5 {
  line-height: 1.2;
  margin-top: 0;
  margin-bottom: 1.5rem;
  font-weight: 400;
  letter-spacing: -.03rem; 
  color:#444;
}
h1 {
  font-size: 2.5rem; 
}
/** Navbar Design Start */
#navbar{
  background-color: #fff;
  border-bottom: 1px solid #efefef;
  height: 60px;
  padding: 0 10px;
}
#navbarBrand{
  text-transform: capitalize;
  font-size: 1rem;
}
#navTypeJs{
  display: inline-block;
}
/** Navbar Design End */
/** Skill Global Start */
#skillSection{
  background-color: #fff;
  margin-top: 60px;
}
#skillSection>div{
  box-shadow: 0 6px 8px rgba(102, 119, 136, 0.03), 0 1px 2px rgba(102, 119, 136, 0.3);
  padding: 5px 20px
}
#skillSection h1{
  text-align: center;
}
#skillSection p{
  font-size: 1.2rem;
  color: #666;
  max-width: 700px;
  margin: 0 auto;
  text-align: center;
}
#skillSection .card {
  -webkit-transition: all 500ms ease;
  transition: all 500ms ease;
  -webkit-box-flex: 1;
  -ms-flex: 1;
  flex: 1;
  height: 200px;
  background: #fff;
  border: 1px solid #efefef;
  border-radius: 6px;
  -webkit-box-shadow: 0 6px 8px rgba(102, 119, 136, 0.03), 0 1px 2px rgba(102, 119, 136, 0.3);
  box-shadow: 0 6px 8px rgba(102, 119, 136, 0.03), 0 1px 2px rgba(102, 119, 136, 0.3);
  margin: .4rem -.5rem; 
}
#skillSection .card:hover{
  background-color: #aaa;
}
#skillSection .card img {
  display: block;
  margin: 0 auto; 
  height: 50px;
}
#skillSection .card h2 {
  font-weight: 500;
  font-size: 1rem;
  margin: 2rem 0 0;
  text-align: center; 
}
#skillSection .card p {
  color: #666;
  margin-bottom: 0; 
}
.front, .back{
  width: 100%;
}
.vue-circle{
  margin-top: -3px;
}
.slot{
  position: absolute;
  left: 50%;
  transform: translate(-50%, 50%);
}
.percent-text{
  font-weight: 200 !important;
  font-size: 18px !important;
  color: #fff !important;
}
.skill-btn a{
  margin: 30px auto 0;
  text-align: center;
  display: block;
  color: #666 !important;
}
/** Skill Global End */
/** LOADER START */
/** Loader Custom */
.lds-ellipsis, .lds-ring{
  z-index: 1;
  position:absolute;
  left:0;
  right:0;
  margin-left:auto;
  margin-right:auto;
  top: 150px;
}
/** Loader Default */
.lds-ring {
  display: inline-block;
  width: 64px;
  height: 64px;
}
#loaderText{
  position: relative; 
  top: 100px; 
  color: #6c757d;
}
.lds-ring div {
  box-sizing: border-box;
  display: block;
  position: absolute;
  width: 51px;
  height: 51px;
  margin: 6px;
  border: 2px solid #6c757d;
  animation: lds-ring 1.2s cubic-bezier(0.5, 0, 0.5, 1) infinite;
  border-color: #6c757d transparent transparent transparent;
}
.lds-ring div:nth-child(1) {
  animation-delay: -0.45s;
}
.lds-ring div:nth-child(2) {
  animation-delay: -0.3s;
}
.lds-ring div:nth-child(3) {
  animation-delay: -0.15s;
}
@keyframes lds-ring {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}
/** LOADER END */
/** Page Media Query Start */
@media (min-width: 1200px){
  #navbar{
    height: 70px;
  }
  #skillSection{
    margin-top: 70px;
  }
}
@media screen and (max-width: 991px) {
  
}
@media screen and (max-width: 767px) {
  
}
@media screen and (max-width: 479px) {
  
}
</style>



