<template>
  <div>
    <!-- Loader -->
    <div class="lds-ring" v-if="loader"><span id="loaderText">Loading</span><div></div><div></div><div></div><div></div></div>
    <!-- Skill Section Start -->
    <div id="skillSection" v-if="dataLoadSignal">
      <b-container>
        <div class="skill-cards">
          <b-row v-for="row in formattedClubsSkills" :key="row" class="skill-row">
            <b-col cols="12" sm="12" md="" lg="" v-for="club in row" :key="club">
              <vue-flip active-hover="true" class="card html-card">
                <div slot="front" class="slot">
                  <img :src="club.img">
                  <h2>{{ club.name }}</h2>
                </div>
                <div slot="back" class="slot">
                  <!-- <vue-circle
                    :progress="club.progress"
                    :size="100"
                    :reverse="false"
                    line-cap="circle"
                    :fill="club.fill"
                    empty-fill="rgba(255, 255, 255, 1)"
                    :animation-start-value="0.0"
                    :start-angle="60"
                    insert-mode="append"
                    :thickness="5"
                    :show-percent="false"
                    @vue-circle-progress="progress"
                    @vue-circle-end="progress_end"
                    class="vue-circle"
                  >
                  </vue-circle> -->
                  <img :src="club.img">
                  <h2>{{ club.name_b }}</h2>
                </div>
              </vue-flip>
            </b-col>
          </b-row>
          <router-link to="skills" class="skill-btn">
            <a class="button">See more skills</a>
          </router-link>
        </div>
      </b-container>
    </div>
    <!-- Skill Section End -->
  </div>
</template>
<script>
import { db } from '../firebase.js';
import Vue from 'vue';
import VueFlip from 'vue-flip';
import VueCircle from 'vue2-circle-progress';

export default {
  components: {
    'vue-flip': VueFlip,
    VueCircle
  },
  data() {
    return {
      jobRetriveData: [],
      loader: true,
      dataLoadSignal: false,
      skillData: [
          { name:'SUMMER', name_b:'গ্রীষ্ম', progress: 100, fill: '#eb6d18', img: 'https://encrypted-tbn0.gstatic.com/images?q=tbn%3AANd9GcSZl59xndTdrWzLyA4Zkx2dBFpi1X3ncnLzGk1h9q3kzWwnsvfM&usqp=CAU'},
          { name:'MONSOON', name_b:'বর্ষা', progress: 80, fill: '#13c0e5', img: 'https://encrypted-tbn0.gstatic.com/images?q=tbn%3AANd9GcSZl59xndTdrWzLyA4Zkx2dBFpi1X3ncnLzGk1h9q3kzWwnsvfM&usqp=CAU'},
          { name:'AUTUMN', name_b:'শর‌ৎ', progress: 70, fill: '#f7af25', img: 'https://encrypted-tbn0.gstatic.com/images?q=tbn%3AANd9GcSZl59xndTdrWzLyA4Zkx2dBFpi1X3ncnLzGk1h9q3kzWwnsvfM&usqp=CAU'},
          { name:'LATE AUTUMN', name_b:'হেমন্ত', progress: 60, fill: { gradient: ["#000", "#41b883"] }, img: 'https://encrypted-tbn0.gstatic.com/images?q=tbn%3AANd9GcSZl59xndTdrWzLyA4Zkx2dBFpi1X3ncnLzGk1h9q3kzWwnsvfM&usqp=CAU'},
          { name:'WINTER', name_b:'শীত', progress: 40, fill: { gradient: ["#41b883", "#000000"] }, img: 'https://encrypted-tbn0.gstatic.com/images?q=tbn%3AANd9GcSZl59xndTdrWzLyA4Zkx2dBFpi1X3ncnLzGk1h9q3kzWwnsvfM&usqp=CAU'},
          { name:'SPRING', name_b:'বসন্ত', progress: 50, fill: { gradient: ["#82dea3"] }, img: 'https://encrypted-tbn0.gstatic.com/images?q=tbn%3AANd9GcSZl59xndTdrWzLyA4Zkx2dBFpi1X3ncnLzGk1h9q3kzWwnsvfM&usqp=CAU'}
      ]
    }
  },
  firestore(){
    return {
      jobRetriveData: db.collection('jobs').orderBy('createdAt','asc')
    }
  },
  methods: {
    
  },
  computed: {
    formattedClubsSkills() {
        return this.skillData.reduce((c, n, i) => {
          if (i % 3 === 0) c.push([]);
          c[c.length - 1].push(n);
          return c;
        }, []);
    }
  },
  watch: {
    'jobRetriveData': function(){
      if(this.jobRetriveData.length > 1){
        this.loader = false;
        this.dataLoadSignal = true;
      }
    }
  }
}
</script>
